<template lang="html">
  <div class="">
    <default-head header="赠送天使豆"></default-head>
    <div id="wrapper" class="page-cnt-box">
      <div class="">
        <div class="text-box" >
          <flm-form-input label="天使豆总额" textAlign="right" color="#fc4d54"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="赠送数量" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="接收方账号" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input type="password" label="支付密码" placeholder="请输入支付密码" textAlign="right"></flm-form-input>
        </div>
      </div>
      <sub-button class="area-foot-btn" text="确定赠送" marginTop="1rem"></sub-button>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import subButton from '@/components/common/subButton'
export default {
  components: { defaultHead, subButton }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.page-cnt-box {
  .text-box {
    background: @whiteColor;
    padding: 0 .22rem;
    border-bottom: 1px solid @lineColor;
  }
  .text-box:last-child {
    border-bottom: none
  }
}
</style>



